import { applyRule } from "../services/rules.js";

export default function ResultsPanel({ mode, votes, revealed }) {
  return (
    <>
      <style>
        {`
        .result-card {
          margin-top: 20px;
          padding: 16px;
          border-radius: 18px;

          background: linear-gradient(
              135deg,
              rgba(236, 72, 153, 0.15),
              rgba(168, 85, 247, 0.1)
            ),
            rgba(15, 23, 42, 0.95);

          border: 1px solid rgba(148, 163, 184, 0.4);
          backdrop-filter: blur(10px);
          box-shadow: 0 12px 26px rgba(15, 23, 42, 0.85);
          color: var(--text-main);
        }

        .result-title {
          font-size: 1.1rem;
          margin: 0 0 8px;
          font-weight: 600;
        }

        .result-value {
          font-size: 1.3rem;
          font-weight: 700;
          color: var(--accent);
        }
        `}
      </style>

      <section className="result-card">
        {!revealed ? (
          <>
            <h2 className="result-title">Rsultat</h2>
            <p>Rvle les votes pour voir la proposition de difficult.</p>
          </>
        ) : (
          <>
            <h2 className="result-title">Rsultat ({mode})</h2>
            <p>
              Valeur propose :{" "}
              <span className="result-value">
                {applyRule(mode, votes) ?? ""}
              </span>
            </p>
          </>
        )}
      </section>
    </>
  );
}
